<template>
  <div class="commonBox" v-loading.fullscreen.lock="reportLoading" element-loading-text="拼命加载中..." element-loading-spinner="el-icon-loading" element-loading-background="rgba(0, 0, 0, 0.8)">
    <div class="psb-header">
      <div class="psbt-title psyreport">您的人格测试结果</div>
      <div class="psbt-back" @click='backList'>返回</div>
    </div>
    <!-- <div class="psb-content" v-scroll> -->
    <div class="psb-content">

        <a :href="iframeUrl" target="_blank">PS：报告地址请点击这里;样式待调整</a>

    </div>
  </div>
</template>

<script>
import { getStuPersonReport, getStuInfo } from "@/api/http.js";
import common from "@/common.js";
export default {
  // demo 地址 ： http://zbxk.shidaceping.com/reportv2/selectsubject/personH5MBTIReport/testid-327-sid-101736-sno-18969886588-ins-1.shtml
  data() {
    
    var currentExam = JSON.parse(localStorage.getItem("zbxk-current-exam")),
      stuInfo = JSON.parse(localStorage.getItem("zbxk-paperSaveParams")),
      _type = localStorage.getItem("zbxk-cp-type"),
      param = {
        test_id: currentExam.test_id,
        school_id: currentExam.school_id,
        sno: stuInfo.sno
      },
      _url = common.getReportUrl(_type,param); 
    console.log(_url);

    return {
      getStuInfoAPI: getStuInfo,
      getStuPersonReportAPI: getStuPersonReport,
      reportLoading: true,

      iframeUrl: _url,

    };
  },
  mounted() {
    var that = this;
    this.$nextTick(function() {
      that.init();
      that.bus.$emit("toChangeTitle", "学业报告");

      //zm测试
      that.setIfram(
        "http://zbxk.shidaceping.com/reportv2/selectsubject/personH5MBTIReport/testid-327-sid-101736-sno-18969886588-ins-1.shtml"
      );
    });
  },
  methods: {
    backList() {
      this.$router.push({ name: "psychological" });
    },
    //初始化
    init() {
      var examInfo = JSON.parse(localStorage.getItem("zbxk-current-exam"));
      this.getStuInfoAPI({ school_id: examInfo.school_id }).then(
        this.success,
        this.error
      );
    },
    success(res) {
      var result = res.data;
      if (result.code === 200) {
        var sno = result.data.student_no;
      } else {
        this.reportLoading = false;
        this.$message.error(result.message);
      }
    },
    error() {
      this.reportLoading = false;
      this.$message.error("网络异常，请重试！");
    },

    //查询报告
    queryReport(params) {
      this.getStuPersonReportAPI(params).then(this.getReortBack, this.error);
    },
    getReortBack(res) {
      var result = res.data;
      if (result.code === 200) {
        //todo:数据结构
        var url = result.data.student_no;
        this.setIframe(url);
      } else {
        this.reportLoading = false;
        this.$message.error(result.message);
      }
    },
    //展示报告
    setIfram(url) {
      this.iframeUrl = url;
    }
  }
};
</script>

<style>
.psbt-title.psyreport::before {
  background: url(../../assets/psyreport.png) no-repeat;
}

/* 正文区域 */
.psc-result-text {
  font-size: 16px;
  color: #284050;
  letter-spacing: 0;
  line-height: 16px;
  margin-bottom: 20px;
}
.psc-result-chart {
  margin-bottom: 20px;
}

.tt-header {
  font-size: 15px;
  font-weight: bold;
  margin-bottom: 30px;
}
.tt-key > div {
  display: inline-block;
  margin-right: 10px;
}
</style>



